<template>
  <div class="bsfooter">
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-md-12">
          <div class="row">
            <div class="col-xs-3 col-sm-3 col-md-3" v-for=" info in footerInfo">
              <h3 class="text-muted">{{info.title}}</h3>
              <ul class="list-unstyled">
                <li v-for="item in info.array">
                  <a href="#">{{item}}</a>
                </li>
              </ul>
            </div>
            <div class="col-md-3 text-right" style="margin-top:50px;">
              <ul class="list-unstyled">
                <li>
                  <img src="@/assets/icon_1.png" alt style="heigth:20px;width:20px;" />
                </li>
                <li>
                  <img src="@/assets/icon-dashboard.png" alt style="heigth:20px;width:20px;" />
                </li>
                <li>
                  <p>
                    <a href>our customers say</a>
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "bsfooter",
  props: ["authorInfo"],
  data() {
    return {
      footerInfo: [{
        title:"友情链接",
        array:['百度','有道','淘宝网','京东']
      },{
        title:"公司产品",
        array:['叮当书城','叮当网','小程序','叮当App']
      },{
        title:"关于我们",
        array:['联系电话','邮箱地址','在线客服','贴吧交流']
      }]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .bsfooter {
  padding: 20px 0;
  margin: 10px 0;
  /* font-size: 16px;
  background: #f8fafa;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 0 !important; */
} 
a {
  color: #000;
}
</style>



